<template>
    <div>
        <div
            @click="showOptions = !showOptions"
            class="dropdown w-dropdown-toggle"
            :class="showOptions ? 'w--open': ''"
        >
            <div class="w-icon-dropdown-toggle"></div>
            <h4 v-if="!selectedOption">{{ title }}</h4>
            <h4 v-else>{{ selectedOption }}</h4>
        </div>
        <nav
            class="dropdown-list w-dropdown-list"
            :style="showOptions ? 'display: block; z-index: 923;': ''"
        >
            <a
                v-for="(option, index) in options"
                :key="index"
                @click="handleInput(option)"
                class="dropdown-link w-dropdown-link w--open"
                :class="{ 'is--template-border': selectedOption === option }"
            >{{ option }}</a>
        </nav>
    </div>
</template>

<script>
export default {
    name: "ufloSelect",
    props: {
        title: {
            type: String,
            required: true,
        },
        options: {
            type: Array,
            required: true,
        },
        hidden: {
            type: String
        }
    },
    model: {
        prop: 'hidden',
        event: 'blur'
    },
    data: () => ({
        showOptions: false,
        selectedOption: '',
    }),
    methods: {
        handleInput (value) {
            this.selectedOption = value;
            this.showOptions = false;
            this.$emit('blur', value);
        }
    }
    
}
</script>

